
body {
  overflow-x: hidden;
}
body * {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: black;
}
#chartVue {
  width: 100%;
  padding: 1.5em 0 1.5em 0;
}
#chartVue .head {
  height: 50px;
  border-bottom: 1px dashed;
}
#chartVue .head b {
  cursor: pointer;
  font-weight: bold;
  margin-left: 1rem;
}
#chartVue .head .arrow {
  font-size: 20px;
  position: relative;
  top: 2px;
  margin-right: 10px;
}
#chartVue .dragAndResize .dragHandle {
  padding: 1.5rem!important;
  height: 100%;
}
#chartVue .dragAndResize .dragHandle .tool {
  position: absolute;
  right: .5rem;
  top: .5rem;
  cursor: pointer;
  font-weight: bold;
}
#chartVue .dragAndResize .dragHandle .chart {
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: default;
}



